import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { SocialSharing } from '@ionic-native/social-sharing';
import { ActionSheetController } from 'ionic-angular';
import { PopoverController } from 'ionic-angular';
import { LocalNotifications } from '@ionic-native/local-notifications';
import * as moment from 'jalali-moment';
import {NotifyPage} from '../notify/notify';
import {DeveloperPage} from '../developer/developer';
import { ToastController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
price="";
faprice="";
show="";
color="green";
tag="دلار";
time="";
vaz=true;
datadate:Array<string>=[];
rate:Array<number>=[];
monthdatadate:Array<string>=[];
monthrate:Array<number>=[];
persian=[];
strw:String="54321";
icon='notifications';
id=1;
data={
  price:0,
  big:"",
  curn:"",
  repat:false
}
pet=null;
dopersian(stri)
{
  let s:String=stri;
  let i=s.length;
  let stro:string="";
  for(let j=0;j<i;j++)
  {
    stro=stro+this.persian[s.charAt(j)];
  }
return stro;

}
showing()
{
if(this.vaz)
{
  this.show=this.faprice;
  this.vaz=false;
  this.color="red";
  this.tag="ریال";
}
else
{
  this.show=this.price;
  this.vaz=true;
  this.color="green";
  this.tag="دلار";
}
}
  constructor(public navCtrl: NavController,public http: Http ,private socialSharing: SocialSharing , public actionSheetCtrl: ActionSheetController , private localNotifications: LocalNotifications,public popoverCtrl: PopoverController,private toastCtrl: ToastController , private storage: Storage) {
    this.storage.get('status').then((val) => {
      if(val)
       this.icon="notifications-off";
     });
     this.pet="kittens";
    this.persian['0']='۰';
    this.persian['1']='۱';
    this.persian['2']='۲';
    this.persian['3']='۳';
    this.persian['4']='۴';
    this.persian['5']='۵';
    this.persian['6']='۶';
    this.persian['7']='۷';
    this.persian['8']='۸';
    this.persian['9']='۹';
    this.persian['.']='.';
    this.persian['-']='-';
    this.persian[':']=':';
    this.persian[',']=',';
this.curent_update();
this.days_update();
this.months_update();
setInterval(() => {
 this.notify();
}, 60000);
this.dopersian(this.strw);
  }
  presentActionSheet() {
   let actionSheet = this.actionSheetCtrl.create({
     title: 'یک گزینه را انتخاب کنید',
     buttons: [
       {
         icon:'timer',
         text: 'اشتراک گذاری ارزش این لحظه',
         handler: () => {
          this.share();
         }
       },{
         icon:'calendar',
         text: 'تغیر قیمت یک ماه پیش (متنی)',
         handler: () => {
           this.month_share();
         }
       },{
         icon:'calendar',
         text: 'تغیر قیمت سه ماه پیش (متنی)',
         handler: () => {
          this.month_sharing();
         }
       },{
         icon:'close',
         text: 'بازگشت',
         role: 'cancel',
         handler: () => {
           console.log('Cancel clicked');
         }
       }
     ]
   });
   actionSheet.present();
 }

 presentPopover() {
   let popover = this.popoverCtrl.create(NotifyPage);
   popover.present();
   popover.onDidDismiss(data => {
     console.log(data);
     if(data!=null){
       if(data.price!=null && data.curn!=null && data.big!=null){
         this.icon='notifications-off';
          this.storage.set('status', true);
         this.storage.set('data',data);
         this.storage.get('data').then((val) => {
           console.log('Your age is', val);
         });
         this.presentToast('اعلان فعال شد');
       }
     }
   })
 }
 presentPopover2() {
   let popover = this.popoverCtrl.create(DeveloperPage);
   popover.present();

 }
 presentToast(text) {
  let toast = this.toastCtrl.create({
    message: text,
    duration: 2000,
    position: 'top'
  });

  toast.onDidDismiss(() => {
    console.log('Dismissed toast');
  });

  toast.present();
}


  not(text)
  {
    this.localNotifications.schedule({
       id: this.id,
       text: text,
       icon: "show",
       smallIcon:"show",
       sound: 'file://assets/sounds/coin.mp3'

    });
    this.id++;
      this.presentToast(text);
  }
  notify()
  {

    this.storage.get('status').then((val) => {
       console.log(val);
   if(val)
   {
     this.curent_update();
     this.storage.get('data').then((val2) => {
       console.log(val2);
       if(val2.big=="big")
       {
       if(val2.curn=="dollar")
       if(parseInt(this.price.replace(',',''))>val2.price)
       {
         let text="ارزش بیت کوین از "+this.dopersian(val2.price)+" دلار بیشتر شد";
         console.log(text);
         this.not(text);
         if(val2.repat)
         {
           this.icon='notifications';
             this.storage.set('status', false);
         }

       }
       if(val2.curn=="rial")
       if(parseInt(this.faprice.replace(',',''))>val2.price)
       {
         let text="ارزش بیت کوین از "+this.dopersian(val2.price)+" ریال بیشتر شد";
         console.log(text);
         this.not(text);
         if(val2.repat)
         {
           this.icon='notifications';
           this.storage.set('status', false);
         }
       }
}
if(val2.big=="less")
{
  if(val2.curn=="dollar")
  if(parseInt(this.price.replace(',',''))<val2.price)
  {
    let text="ارزش بیت کوین از "+this.dopersian(val2.price)+" دلار کمتر شد";
    console.log(text);
    this.not(text);
    if(val2.repat)
    {
      this.icon='notifications';
      this.storage.set('status', false);
    }

  }
  if(val2.curn=="rial")
  if(parseInt(this.faprice.replace(',',''))<val2.price)
  {
    let text="ارزش بیت کوین از "+this.dopersian(val2.price)+" ریال کمتر شد";
    console.log(text);
    this.not(text);
    if(val2.repat)
    {
      this.icon='notifications';
      this.storage.set('status', false);
    }
  }

}


     });


   }
 });
  }
  share()
  {
    let str="";
    str=str+"ارزش بیت کوین در این لحظه\n";
    str=str+this.dopersian(this.price);
    str=str+" دلار";
    str=str+"\n";
    str=str+this.dopersian(this.faprice);
    str=str+" ریال";
    console.log(str);
    this.socialSharing.share(str,'ارزش فعلی بیت کوین');
  }
  month_share()
  {
    let str="";
    str=str+"ارزش بیت کوین از یکماه پیش\n";
    let i=this.rate.length;
    for(let j=0;j<i;j++)
  str=str+this.datadate[j]+" = "+this.dopersian(this.rate[j].toString())+" دلار "+"\n";
    this.socialSharing.share(str,'ارزش بیت کوین از یکماه پیش');
    console.log(str);
  };
  month_sharing()
  {
    let str="";
    str=str+"ارزش بیت کوین از سه ماه پیش\n";
    let i=this.monthrate.length;
    for(let j=0;j<i;j++)
  str=str+this.monthdatadate[j]+" = "+this.dopersian(this.monthrate[j].toString())+" دلار "+"\n";
  this.socialSharing.share(str,'ارزش بیت کوین از سه ماه پیش');
  console.log(str);

};
    public lineChartData:Array<any> = [
      {data: this.rate, label: 'ارزش بیت کوین'},

    ];

    public lineChartLabels:Array<any> = this.datadate;
    public lineChartOptions:any = {
      responsive: true
    };

    public lineChartColors:Array<any> = [
      { // grey
        backgroundColor: '#FFC107',
        borderColor: '#FF9800',
        pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff',
        pointHoverBackgroundColor: '#fff',
        pointHoverBorderColor: 'rgba(148,159,177,0.8)'
      }
    ];
    public lineChartLegend:boolean = true;
    public lineChartType:string = 'line';

    public chartClicked(e:any):void {
      console.log(e);
    }

    public chartHovered(e:any):void {
      console.log(e);
    }
    public lineChartData1:Array<any> = [
      {data: this.monthdatadate, label: 'ارزش بیت کوین'},

    ];
    public lineChartLabels1:Array<any> = this.monthdatadate;
    public lineChartOptions1:any = {
      responsive: true
    };

    public lineChartColors1:Array<any> = [
      { // grey
        backgroundColor: '#FFC107',
        borderColor: '#FF9800',
        pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff',
        pointHoverBackgroundColor: '#fff',
        pointHoverBorderColor: 'rgba(148,159,177,0.8)'
      }
    ];
    public lineChartLegend1:boolean = true;
    public lineChartType1:string = 'line';

    public chartClicked1(e:any):void {
      console.log(e);
    }

    public chartHovered1(e:any):void {
      console.log(e);
    }
    curent_update()
    {
      this.http.get("https://api.coindesk.com/v1/bpi/currentprice/IRR.json").map(res => res.json()).subscribe(data => {
        this.price=data.bpi.USD['rate'];
        this.faprice=data.bpi.IRR['rate'];
        this.time=data.time.updated;
if(  this.tag=="دلار")
this.show=this.price;
else
this.show=this.faprice;
    console.log("updated");
      });
    }
    days_update()
    {
      this.http.get("https://api.coindesk.com/v1/bpi/historical/close.json?start="+moment().utc().subtract(1,'day').subtract(1,'month').format('YYYY-MM-DD')+"&end="+moment().utc().subtract(1,'day').format('YYYY-MM-DD')+"").map(res => res.json()).subscribe(data => {
    let i=Object.keys(data.bpi).length;
    for(let y=0;y<i;y++)
    {

    let str=moment().utc().subtract(1,'day').subtract(1,'month').add(y,'day').format("YYYY-MM-DD");
    this.datadate.push(this.dopersian(moment(str).format('jYYYY-jM-jD')));
    this.rate.push(Number(data.bpi[str]));
    }
    let _lineChartData:Array<any> = new Array(this.lineChartData.length);
    for (let i = 0; i < this.lineChartData.length; i++) {
      _lineChartData[i] = {data: new Array(this.lineChartData[i].data.length), label: this.lineChartData[i].label};
      for (let j = 0; j < this.lineChartData[i].data.length; j++) {
        _lineChartData[i].data[j] = this.rate[j];
      }
    }
    this.lineChartData = _lineChartData;
      });
    }
    months_update()
    {
      this.http.get("https://api.coindesk.com/v1/bpi/historical/close.json?start="+moment().utc().subtract(1,'day').subtract(3,'month').format('YYYY-MM-DD')+"&end="+moment().utc().subtract(1,'day').format('YYYY-MM-DD')+"").map(res => res.json()).subscribe(data => {
      let i=Object.keys(data.bpi).length;
      for(let y=0;y<i;y++)
      {

      let str=moment().utc().subtract(1,'day').subtract(3,'month').add(y,'day').format("YYYY-MM-DD");
      this.monthdatadate.push(this.dopersian(moment(str).format('jYYYY-jM-jD')));
      this.monthrate.push(Number(data.bpi[str]));
      }
      let _lineChartData1:Array<any> = new Array(this.lineChartData1.length);
      for (let i = 0; i < this.lineChartData1.length; i++) {
      _lineChartData1[i] = {data: new Array(this.lineChartData1[i].data.length), label: this.lineChartData1[i].label};
      for (let j = 0; j < this.lineChartData1[i].data.length; j++) {
        _lineChartData1[i].data[j] = this.monthrate[j];
      }
      }
      this.lineChartData1 = _lineChartData1;
      });
    }

    doRefresh(refresher) {
        console.log('Begin async operation', refresher);

        setTimeout(() => {
          this.curent_update();
          refresher.complete();
        }, 2000);
      }
      donotify()
      {
        this.storage.get('status').then((val) => {

        if (!val)
        {
          this. presentPopover();
        }
        else
        {
          this.icon='notifications';
            this.storage.set('status', false);
          this.presentToast('اعلان غیرفعال شد');

        }

 });


      }
      appstore()
      {
        window.open("market://details?id=io.github.mojixyz.coinnema", '_system');
      }

}



